<template>
    <v-container>
        <v-layout>
            <v-flex xs12 md4 offset-md4 class="mt-5 pt-5">
                <v-card>
                    <v-card-title
                        class="headline indigo white--text"
                        primary-title
                    >
                        Already have an account?
                    </v-card-title>
                        <v-form
                        @submit.prevent="logIn"
                        class="pa-4"
                        ref="form"
                        v-model="valid"
                        lazy-validation
                    >

                        <v-text-field
                        v-model="email"
                        :rules="emailRules"
                        label="E-mail"
                        required
                        ></v-text-field>

                        <v-text-field
                        label="Password"
                        v-model="password"
                        :rules="passwordRules"
                        required
                        :type="'password'"
                        ></v-text-field>
                        <p v-if="error" class="red--text"> {{ error }} </p>
                        <v-btn
                        :disabled="!valid"
                        class="indigo white--text text-capitalize mt-4"
                        type="submit"
                        >
                        Log In
                        </v-btn>
                    </v-form>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
import firebase from 'firebase'
import './../../node_modules/nprogress/nprogress.css'
import NProgress from 'nprogress'

  export default {
    data: () => ({
      valid: true,
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid'
      ],
      passwordRules: [
        v => !!v || 'Password is required'
      ],
      password: '',
      error: null
    }),

    methods: {
      logIn() {
            NProgress.start()
        if (this.$refs.form.validate()) {
            firebase.auth().signInWithEmailAndPassword(this.email, this.password)
            .then(() => {
              NProgress.done()
              this.error = null
              this.$router.push({ name: 'users' })
            })
            .catch(err => this.error = err)
        }
      }
    }
  }
</script>